@import url(https://fonts.googleapis.com/css?family=Ubuntu:300,400,500,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.wrapper {
  font-family: 'Ubuntu', sans-serif;
  font-size: 1em;
  padding: 4vh 6vw;
}

.content {
  padding: 2%;
}
.header {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
}

.status p {
    margin: 1vw 13vw;
}

a:hover h1 {
    color: #007bff;
}

main a:hover {
    text-decoration: none;
}

main h1 {
    font-size: 1.25rem;
}

.logo {
    height: 10vmax;
}

@media only screen and (min-width: 768px) {
    .status p {
        margin: -0.5vw 8vw;
    }

    main h1 {
        font-size: 2.25rem;
    }
}

@media only screen and (min-width: 1024px) {
    .status p {
        margin: -1.5vw 6vw;
    }
}

@media only screen and (min-width: 1280px) {
    .status p {
        margin: 18px 4.5vw;
    }
}
:focus {
    outline: none;
}

a:hover {
    text-decoration: none;
}

h3 {
    color: white;
}

/* Position and sizing of burger button */
.bm-burger-button {
    position: fixed;
    width: 36px;
    height: 30px;
    right: 6vw;
    top: 36px;
}

/* Color/shape of burger icon bars */
.bm-burger-bars {
    background: #373a47;
}

/* Color/shape of burger icon bars on hover*/
.bm-burger-bars-hover {
    background: #2463D5;
}

/* Position and sizing of clickable cross button */
.bm-cross-button {
    height: 36px !important;
    width: 36px !important;
    right: 6.25vw !important;
    top: 28px !important;
}

/* Color/shape of close button cross */
.bm-cross {
    background: white;
    width: 4px !important;
    height: 32px !important;
}

/*
  Sidebar wrapper styles
  Note: Beware of modifying this element as it can break the animations - you should not need to touch it in most cases
  */
.bm-menu-wrap {
    position: fixed;
    height: 100%;
    width: 100vw !important;
}

/* General sidebar styles */
.bm-menu {
    background: #2463D5;
    text-align: right;
    margin: 0 -.5px;
    padding: 6em 7vw 0;
    font-size: 1.15em;
}

/* Wrapper for item list */
.bm-item-list a,
.linky {
    color: #d1d1cc;
}

.bm-item-list a:hover,
.linky:hover {
    color: #b8b7ad;
}

.bm-item-list ul {
    list-style-type: none;
}

.last {
    margin: .75em 0 1em !important;
}

.only {
    margin: 0 0 1em !important;
}

/* Individual item */
.bm-item {
    display: inline-block;
}

/* Styling of overlay */
.bm-overlay {
    background: rgba(0, 0, 0, 0.2) !important;
}

@media only screen and (min-width: 768px) {
    .bm-menu-wrap {
        width: 50vw !important;
    }
}

@media only screen and (min-width: 1024px) {
    .bm-menu-wrap {
        width: 30vw !important;
    }
}
section {
    margin: 20px 0;
    border: 3px solid #286ee9;
    border-radius: 0 0 15px 15px;
}

section h2 {
    padding: 1vw 2vw;
    background: #286ee9;
    margin: -1px;
    color: white;
}

section ul {
    list-style: none;
    padding: 0 0 5px;
    margin: 1vw 2vw;
}

section li {
    line-height: 2em;
}

section li a {
    color: #0056b3;
    font-weight: 500;
}

section li a:hover {
    color: #286ee9;
}
h2 a {
    color: white;
    font-size: 1.25em;
}

h2 a:hover {
    color: rgba(255, 255, 255, 0.7);
}

h2 {
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.login {
    width: 90%;
    margin: 1vh auto;
}

.email {
    margin: 1rem 0;
}

@media only screen and (min-width: 768px) {
    .login {
        width: 50%;
        margin: 1vh auto;
    }
}

@media only screen and (min-width: 1280px) {
    .login {
        width: 30%;
        margin: 1vh auto;
    }
}
.signup {
    width: 50%;
    margin: 1vh auto;
}
.footer {
    display: flex;
    justify-content: space-between;
    align-items: baseline;
    margin-top: 20px;
}

a h3 {
    display: inline;
    color: #286ee9;
}

a:hover h3 {
    color: #7da6ea;
}
.profile {
    margin: 2vh auto;
    max-width: 50%;
}

.profileLabel {
    text-align: right;
}
.notebookForm {
    margin: 10vh 2vw;
}
.noteForm {
    margin: 10vh 2vw;
}
